"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Textarea } from "@/components/ui/textarea"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { format } from "date-fns"

interface Book {
  id: string
  title: string
  author: string
  isbn: string
  category: string
  publisher: string
  publishYear: number
  totalCopies: number
  availableCopies: number
  status: "available" | "limited" | "out_of_stock"
  location: string
  description?: string
}

interface BorrowRecord {
  id: string
  bookId: string
  bookTitle: string
  studentId: string
  studentName: string
  borrowDate: string
  dueDate: string
  returnDate?: string
  status: "borrowed" | "returned" | "overdue"
  fineAmount?: number
}

export default function LibraryPage() {
  const [searchTerm, setSearchTerm] = useState("")
  const [isBookDialogOpen, setIsBookDialogOpen] = useState(false)
  const [isBorrowDialogOpen, setIsBorrowDialogOpen] = useState(false)
  const [editingBook, setEditingBook] = useState<Book | null>(null)
  const [selectedBookForBorrow, setSelectedBookForBorrow] = useState<string>("")

  // Mock data
  const [books, setBooks] = useState<Book[]>([
    {
      id: "1",
      title: "Advanced Mathematics",
      author: "Dr. John Smith",
      isbn: "978-0123456789",
      category: "Mathematics",
      publisher: "Academic Press",
      publishYear: 2023,
      totalCopies: 10,
      availableCopies: 7,
      status: "available",
      location: "Section A - Shelf 1",
      description: "Comprehensive guide to advanced mathematical concepts"
    },
    {
      id: "2",
      title: "Physics Fundamentals",
      author: "Prof. Sarah Wilson",
      isbn: "978-0987654321",
      category: "Physics",
      publisher: "Science Publications",
      publishYear: 2022,
      totalCopies: 8,
      availableCopies: 2,
      status: "limited",
      location: "Section B - Shelf 3",
      description: "Essential physics concepts for high school students"
    },
    {
      id: "3",
      title: "World History",
      author: "Dr. Michael Brown",
      isbn: "978-0456789123",
      category: "History",
      publisher: "Historical Books Ltd",
      publishYear: 2021,
      totalCopies: 5,
      availableCopies: 0,
      status: "out_of_stock",
      location: "Section C - Shelf 2",
      description: "Comprehensive overview of world history"
    }
  ])

  const [borrowRecords, setBorrowRecords] = useState<BorrowRecord[]>([
    {
      id: "1",
      bookId: "1",
      bookTitle: "Advanced Mathematics",
      studentId: "1",
      studentName: "John Doe",
      borrowDate: "2024-01-15",
      dueDate: "2024-02-15",
      status: "borrowed"
    },
    {
      id: "2",
      bookId: "2",
      bookTitle: "Physics Fundamentals",
      studentId: "2",
      studentName: "Jane Smith",
      borrowDate: "2024-01-10",
      dueDate: "2024-02-10",
      returnDate: "2024-02-08",
      status: "returned"
    },
    {
      id: "3",
      bookId: "1",
      bookTitle: "Advanced Mathematics",
      studentId: "3",
      studentName: "Mike Johnson",
      borrowDate: "2024-01-05",
      dueDate: "2024-02-05",
      status: "overdue",
      fineAmount: 5.00
    }
  ])

  const filteredBooks = books.filter(book =>
    book.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    book.author.toLowerCase().includes(searchTerm.toLowerCase()) ||
    book.category.toLowerCase().includes(searchTerm.toLowerCase()) ||
    book.isbn.includes(searchTerm)
  )

  const filteredBorrowRecords = borrowRecords.filter(record =>
    record.bookTitle.toLowerCase().includes(searchTerm.toLowerCase()) ||
    record.studentName.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const handleAddBook = () => {
    setEditingBook(null)
    setIsBookDialogOpen(true)
  }

  const handleEditBook = (book: Book) => {
    setEditingBook(book)
    setIsBookDialogOpen(true)
  }

  const handleDeleteBook = (id: string) => {
    setBooks(books.filter(book => book.id !== id))
  }

  const handleSaveBook = (formData: FormData) => {
    const totalCopies = parseInt(formData.get("totalCopies") as string)
    const availableCopies = parseInt(formData.get("availableCopies") as string)
    
    let status: "available" | "limited" | "out_of_stock" = "available"
    if (availableCopies === 0) {
      status = "out_of_stock"
    } else if (availableCopies <= totalCopies * 0.3) {
      status = "limited"
    }

    const bookData = {
      id: editingBook?.id || Date.now().toString(),
      title: formData.get("title") as string,
      author: formData.get("author") as string,
      isbn: formData.get("isbn") as string,
      category: formData.get("category") as string,
      publisher: formData.get("publisher") as string,
      publishYear: parseInt(formData.get("publishYear") as string),
      totalCopies,
      availableCopies,
      status,
      location: formData.get("location") as string,
      description: formData.get("description") as string
    }

    if (editingBook) {
      setBooks(books.map(b => b.id === editingBook.id ? bookData : b))
    } else {
      setBooks([...books, bookData])
    }

    setIsBookDialogOpen(false)
    setEditingBook(null)
  }

  const handleBorrowBook = (bookId: string) => {
    setSelectedBookForBorrow(bookId)
    setIsBorrowDialogOpen(true)
  }

  const handleSaveBorrow = (formData: FormData) => {
    const borrowDate = new Date()
    const dueDate = new Date()
    dueDate.setDate(dueDate.getDate() + 30) // 30 days borrowing period

    const book = books.find(b => b.id === selectedBookForBorrow)
    if (!book) return

    const borrowRecord: BorrowRecord = {
      id: Date.now().toString(),
      bookId: selectedBookForBorrow,
      bookTitle: book.title,
      studentId: formData.get("studentId") as string,
      studentName: formData.get("studentName") as string,
      borrowDate: format(borrowDate, "yyyy-MM-dd"),
      dueDate: format(dueDate, "yyyy-MM-dd"),
      status: "borrowed"
    }

    setBorrowRecords([...borrowRecords, borrowRecord])

    // Update book availability
    setBooks(books.map(b => 
      b.id === selectedBookForBorrow 
        ? { 
            ...b, 
            availableCopies: b.availableCopies - 1,
            status: b.availableCopies - 1 === 0 ? "out_of_stock" : 
                   b.availableCopies - 1 <= b.totalCopies * 0.3 ? "limited" : "available"
          }
        : b
    ))

    setIsBorrowDialogOpen(false)
    setSelectedBookForBorrow("")
  }

  const handleReturnBook = (recordId: string) => {
    const record = borrowRecords.find(r => r.id === recordId)
    if (!record) return

    const returnDate = new Date()
    const dueDate = new Date(record.dueDate)
    const isOverdue = returnDate > dueDate
    const daysDiff = Math.ceil((returnDate.getTime() - dueDate.getTime()) / (1000 * 3600 * 24))
    const fineAmount = isOverdue ? daysDiff * 0.5 : 0 // $0.50 per day fine

    setBorrowRecords(borrowRecords.map(r =>
      r.id === recordId
        ? {
            ...r,
            returnDate: format(returnDate, "yyyy-MM-dd"),
            status: "returned" as const,
            fineAmount: fineAmount > 0 ? fineAmount : undefined
          }
        : r
    ))

    // Update book availability
    setBooks(books.map(b => 
      b.id === record.bookId 
        ? { 
            ...b, 
            availableCopies: b.availableCopies + 1,
            status: b.availableCopies + 1 === b.totalCopies ? "available" :
                   b.availableCopies + 1 <= b.totalCopies * 0.3 ? "limited" : "available"
          }
        : b
    ))
  }

  const getLibraryStats = () => {
    const totalBooks = books.reduce((sum, book) => sum + book.totalCopies, 0)
    const availableBooks = books.reduce((sum, book) => sum + book.availableCopies, 0)
    const borrowedBooks = borrowRecords.filter(r => r.status === "borrowed").length
    const overdueBooks = borrowRecords.filter(r => r.status === "overdue").length
    const totalFines = borrowRecords.reduce((sum, r) => sum + (r.fineAmount || 0), 0)

    return {
      totalBooks,
      availableBooks,
      borrowedBooks,
      overdueBooks,
      totalFines
    }
  }

  const stats = getLibraryStats()

  const getStatusColor = (status: string) => {
    switch (status) {
      case "available": case "returned": return "default"
      case "limited": case "borrowed": return "secondary"
      case "out_of_stock": case "overdue": return "destructive"
      default: return "outline"
    }
  }

  return (
    <div className="flex-1 space-y-6 p-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Library</h1>
          <p className="text-muted-foreground">
            Manage books, borrowing, and library inventory
          </p>
        </div>
      </div>

      {/* Library Statistics */}
      <div className="grid gap-4 md:grid-cols-5">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Books</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalBooks}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Available</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.availableBooks}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Borrowed</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{stats.borrowedBooks}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Overdue</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{stats.overdueBooks}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Fines</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">${stats.totalFines.toFixed(2)}</div>
          </CardContent>
        </Card>
      </div>

      <Tabs defaultValue="books" className="space-y-4">
        <TabsList>
          <TabsTrigger value="books">Book Inventory</TabsTrigger>
          <TabsTrigger value="borrowing">Borrowing Records</TabsTrigger>
        </TabsList>

        <TabsContent value="books" className="space-y-4">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Book Inventory</CardTitle>
                  <CardDescription>
                    Manage library book collection
                  </CardDescription>
                </div>
                <Button onClick={handleAddBook}>
                  Add New Book
                </Button>
              </div>
              <div className="flex items-center space-x-2">
                <Input
                  placeholder="Search books..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="max-w-sm"
                />
              </div>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Title</TableHead>
                    <TableHead>Author</TableHead>
                    <TableHead>Category</TableHead>
                    <TableHead>ISBN</TableHead>
                    <TableHead>Copies</TableHead>
                    <TableHead>Available</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Location</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredBooks.map((book) => (
                    <TableRow key={book.id}>
                      <TableCell className="font-medium">{book.title}</TableCell>
                      <TableCell>{book.author}</TableCell>
                      <TableCell>{book.category}</TableCell>
                      <TableCell className="font-mono text-sm">{book.isbn}</TableCell>
                      <TableCell>{book.totalCopies}</TableCell>
                      <TableCell>{book.availableCopies}</TableCell>
                      <TableCell>
                        <Badge variant={getStatusColor(book.status)}>
                          {book.status.replace("_", " ")}
                        </Badge>
                      </TableCell>
                      <TableCell className="text-sm">{book.location}</TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleEditBook(book)}
                          >
                            Edit
                          </Button>
                          {book.availableCopies > 0 && (
                            <Button
                              variant="secondary"
                              size="sm"
                              onClick={() => handleBorrowBook(book.id)}
                            >
                              Borrow
                            </Button>
                          )}
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => handleDeleteBook(book.id)}
                          >
                            Delete
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="borrowing" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Borrowing Records</CardTitle>
              <CardDescription>
                Track book borrowing and returns
              </CardDescription>
              <div className="flex items-center space-x-2">
                <Input
                  placeholder="Search borrowing records..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="max-w-sm"
                />
              </div>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Book Title</TableHead>
                    <TableHead>Student</TableHead>
                    <TableHead>Borrow Date</TableHead>
                    <TableHead>Due Date</TableHead>
                    <TableHead>Return Date</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Fine</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredBorrowRecords.map((record) => (
                    <TableRow key={record.id}>
                      <TableCell className="font-medium">{record.bookTitle}</TableCell>
                      <TableCell>{record.studentName}</TableCell>
                      <TableCell>{format(new Date(record.borrowDate), "MMM d, yyyy")}</TableCell>
                      <TableCell>{format(new Date(record.dueDate), "MMM d, yyyy")}</TableCell>
                      <TableCell>
                        {record.returnDate ? format(new Date(record.returnDate), "MMM d, yyyy") : "-"}
                      </TableCell>
                      <TableCell>
                        <Badge variant={getStatusColor(record.status)}>
                          {record.status}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        {record.fineAmount ? `$${record.fineAmount.toFixed(2)}` : "-"}
                      </TableCell>
                      <TableCell>
                        {record.status === "borrowed" && (
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleReturnBook(record.id)}
                          >
                            Return Book
                          </Button>
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Add/Edit Book Dialog */}
      <Dialog open={isBookDialogOpen} onOpenChange={setIsBookDialogOpen}>
        <DialogContent className="sm:max-w-[600px] max-h-[80vh] overflow-y-auto">
          <form action={handleSaveBook}>
            <DialogHeader>
              <DialogTitle>
                {editingBook ? "Edit Book" : "Add New Book"}
              </DialogTitle>
              <DialogDescription>
                {editingBook 
                  ? "Update book information below."
                  : "Enter book details to add to the library inventory."
                }
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="title" className="text-right">
                  Title
                </Label>
                <Input
                  id="title"
                  name="title"
                  defaultValue={editingBook?.title || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="author" className="text-right">
                  Author
                </Label>
                <Input
                  id="author"
                  name="author"
                  defaultValue={editingBook?.author || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="isbn" className="text-right">
                  ISBN
                </Label>
                <Input
                  id="isbn"
                  name="isbn"
                  defaultValue={editingBook?.isbn || ""}
                  className="col-span-3"
                  placeholder="978-0123456789"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="category" className="text-right">
                  Category
                </Label>
                <Select name="category" defaultValue={editingBook?.category || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Mathematics">Mathematics</SelectItem>
                    <SelectItem value="Physics">Physics</SelectItem>
                    <SelectItem value="Chemistry">Chemistry</SelectItem>
                    <SelectItem value="Biology">Biology</SelectItem>
                    <SelectItem value="English">English</SelectItem>
                    <SelectItem value="History">History</SelectItem>
                    <SelectItem value="Geography">Geography</SelectItem>
                    <SelectItem value="Computer Science">Computer Science</SelectItem>
                    <SelectItem value="Literature">Literature</SelectItem>
                    <SelectItem value="Reference">Reference</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="publisher" className="text-right">
                  Publisher
                </Label>
                <Input
                  id="publisher"
                  name="publisher"
                  defaultValue={editingBook?.publisher || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="publishYear" className="text-right">
                  Publish Year
                </Label>
                <Input
                  id="publishYear"
                  name="publishYear"
                  type="number"
                  min="1900"
                  max="2030"
                  defaultValue={editingBook?.publishYear || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="totalCopies" className="text-right">
                  Total Copies
                </Label>
                <Input
                  id="totalCopies"
                  name="totalCopies"
                  type="number"
                  min="1"
                  defaultValue={editingBook?.totalCopies || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="availableCopies" className="text-right">
                  Available Copies
                </Label>
                <Input
                  id="availableCopies"
                  name="availableCopies"
                  type="number"
                  min="0"
                  defaultValue={editingBook?.availableCopies || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="location" className="text-right">
                  Location
                </Label>
                <Input
                  id="location"
                  name="location"
                  defaultValue={editingBook?.location || ""}
                  className="col-span-3"
                  placeholder="e.g., Section A - Shelf 1"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-start gap-4">
                <Label htmlFor="description" className="text-right pt-2">
                  Description
                </Label>
                <Textarea
                  id="description"
                  name="description"
                  defaultValue={editingBook?.description || ""}
                  className="col-span-3"
                  placeholder="Brief description of the book"
                />
              </div>
            </div>
            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => setIsBookDialogOpen(false)}>
                Cancel
              </Button>
              <Button type="submit">
                {editingBook ? "Update Book" : "Add Book"}
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>

      {/* Borrow Book Dialog */}
      <Dialog open={isBorrowDialogOpen} onOpenChange={setIsBorrowDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <form action={handleSaveBorrow}>
            <DialogHeader>
              <DialogTitle>Borrow Book</DialogTitle>
              <DialogDescription>
                Enter student details to borrow the selected book.
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="studentName" className="text-right">
                  Student Name
                </Label>
                <Input
                  id="studentName"
                  name="studentName"
                  className="col-span-3"
                  placeholder="Enter student name"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="studentId" className="text-right">
                  Student ID
                </Label>
                <Input
                  id="studentId"
                  name="studentId"
                  className="col-span-3"
                  placeholder="Enter student ID"
                  required
                />
              </div>
              <div className="text-sm text-muted-foreground">
                <p>Borrowing period: 30 days</p>
                <p>Fine: $0.50 per day for overdue books</p>
              </div>
            </div>
            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => setIsBorrowDialogOpen(false)}>
                Cancel
              </Button>
              <Button type="submit">
                Borrow Book
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  )
}
